/* Food Detail Screen - Matching Flutter stadium_food app design */
.food-detail-screen {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #ffffff;
  position: relative;
}

/* Extra safety: tiny white overlay at content top to mask any subpixel seam */
.food-detail-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0; right: 0;
  height: 4px;
  background: #ffffff;
  pointer-events: none;
}

/* Header with image (matching Flutter SliverAppBar) */
.food-detail-header {
  position: relative;
  height: 40vh;
  background: linear-gradient(135deg, #3D70FF 0%, #5A84F8 100%);
  overflow: hidden;
}
/* Note: Back/Fav button styles live in FoodHeader.css */

.food-image-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.food-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block; /* remove inline-image baseline gap */
}

.food-image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
}

.placeholder-icon {
  font-size: 100px;
  color: rgb(255, 255, 255);
}

.header-curve { display: none; }

/* Mask any subpixel seam between image and content */
.food-detail-header::after {
  content: '';
  position: absolute;
  left: 0; right: 0; bottom: 0;
  height: 3px; /* thicker to hide subpixel seam */
  background: #ffffff;
  pointer-events: none;
}

/* Content */
.food-detail-content {
  flex: 1;
  padding: 0 16px 120px;
  background: #ffffff;
  position: relative; /* for top white overlay */
}

.food-card {
  background: #ffffff;
  border-radius: 0;
  margin-top: 0; /* no overlap */
  box-shadow: none;
  padding: 24px 20px 28px;
}

/* Centered layout like Figma inside card */
.food-card .food-info-section { margin-bottom: 18px; }
.food-card .food-title-row { display: block; text-align: center; margin-bottom: 10px; }
.food-card .food-name { margin: 0; text-align: center; font-size: 22px; font-weight: 700; color: #3A3A3A; }
.food-card .food-rating { display: none; }
.food-card .food-meta { justify-content: center; gap: 14px; margin-top: 6px; }
.food-card .food-price { font-size: 24px; color: #3D70FF; font-weight: 800; }
.food-card .order-count { color: #5F6B7A; }

.food-card .section-title { text-align: center; color: #404B5A; margin-top: 8px; }
.food-card .food-description { text-align: center; color: #6B7280; }

/* Food info section */
.food-info-section {
  margin-bottom: 30px;
}

.food-title-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.food-name {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  margin: 0;
  flex: 1;
  margin-right: 16px;
}

.food-rating {
  display: flex;
  align-items: center;
  gap: 4px;
  background: white;
  padding: 6px 12px;
  border-radius: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.star-icon {
  font-size: 16px;
  color: #FFD700;
}

.rating-value {
  font-size: 14px;
  font-weight: 600;
  color: #333;
}

.rating-count {
  font-size: 12px;
  color: #666;
}

.food-meta {
  display: flex;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.food-price {
  font-size: 20px;
  font-weight: 700;
  color: #3D70FF;
}

.food-prep-time {
  display: flex;
  align-items: center;
  gap: 4px;
  color: #666;
  font-size: 14px;
}

.time-icon {
  font-size: 16px;
}

.order-count {
  color: #666;
  font-size: 14px;
}

/* Sections */
.section {
  margin-bottom: 30px;
}

.section-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 12px 0;
}

.food-description {
  font-size: 14px;
  line-height: 1.6;
  color: #666;
  margin: 0;
}

/* Allergens */
.allergens-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.allergen-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 4px 0;
  font-size: 14px;
  color: #666;
}

.bullet-point {
  color: #3D70FF;
  font-weight: bold;
  font-size: 16px;
}

.no-data-text {
  text-align: center;
  color: #999;
  font-size: 14px;
  font-style: italic;
  margin: 20px 0;
}

/* Testimonials */
.testimonials-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.testimonial-item {
  background: white;
  padding: 16px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.testimonial-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.testimonial-rating {
  display: flex;
  gap: 2px;
}

.testimonial-rating .star {
  font-size: 14px;
}

.testimonial-rating .star.filled {
  color: #FFD700;
}

.testimonial-rating .star.empty {
  color: #E0E0E0;
}

.testimonial-date {
  font-size: 12px;
  color: #999;
}

.testimonial-text {
  font-size: 14px;
  line-height: 1.5;
  color: #333;
  margin: 8px 0;
}

.testimonial-author {
  font-size: 12px;
  color: #666;
  font-style: italic;
  margin: 0;
}

/* Bottom bar (matching Flutter bottomNavigationBar) */
.food-detail-bottom-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
  padding: 10px 16px 16px;
  display: flex;
  align-items: center;
  gap: 16px;
  box-shadow: 0 -2px 12px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.favorite-button {
  background: white;
  border: 2px solid #E0E0E0;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.favorite-button:hover {
  border-color: #3D70FF;
  transform: scale(1.05);
}

.favorite-button.liked {
  background: #3D70FF;
  border-color: #3D70FF;
}

.favorite-button svg {
  font-size: 20px;
  color: #666;
}

.favorite-button.liked svg {
  color: white;
}

.add-to-cart-button {
  flex: 1;
  background: #3D70FF;
  color: white;
  border: none;
  border-radius: 12px;
  padding: 14px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add-to-cart-button:hover {
  background: #3161EA;
  transform: translateY(-1px);
}

.cart-icon {
  font-size: 18px;
}

/* Loading and Error States */
.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  text-align: center;
  min-height: 50vh;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #E0E0E0;
  border-top: 3px solid #3D70FF;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-text {
  color: #666;
  font-size: 16px;
  margin-bottom: 20px;
}

.retry-button {
  background: #3D70FF;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s ease;
}

.retry-button:hover {
  background: #3161EA;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .food-detail-header {
    height: 35vh;
  }
  
  .fd-back-button {
    top: 40px;
  }
  
  .food-name {
    font-size: 20px;
  }
  
  .food-title-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .food-rating {
    align-self: flex-end;
  }
  
  .food-meta {
    gap: 16px;
  }
  
  .food-price {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .food-detail-content {
    padding: 0 12px 120px;
  }
  
  .food-card {
    padding: 20px 16px 24px;
    border-radius: 0; /* keep flat card on mobile */
    margin-top: 0; /* no overlap that can create a seam */
  }
  
  .food-detail-header {
    height: 30vh;
  }
  
  .food-name {
    font-size: 18px;
  }
  
  .section-title {
    font-size: 16px;
  }
  
  .food-detail-bottom-bar {
    padding: 8px 12px 12px;
    gap: 12px;
  }
  
  .favorite-button {
    width: 44px;
    height: 44px;
  }
  
  .add-to-cart-button {
    padding: 12px 16px;
    font-size: 14px;
  }
}
